<template>
    <div id="A_Container_Wrap">
        <div id="A_Container">
            <div class="bg_grey">
                <modal v-if="changePassword">
                    <div slot="body">
                        <!--   -->
                        <div class="change_pw_view popup">
                            <div class="change_pw_wrap popup_wrap">
                                <div class="tit">
                                    <p> </p>
                                    <span class="close" @click="changePasswordClose"><img
                                        src="../../../static/img/esc_icon.png" alt=""></span>
                                </div>
                                <form>
                                    <fieldset>
                                        <!--<legend> </legend>-->
                                        <div id="pw_change_Div1" class="join_input">
                                            <input type="password" placeholder=" " title=" "
                                                   class="mb20 account_pwd" v-model="oldPwd">
                                            <input type="password" class="bb_none account_pwd" placeholder=" "
                                                   title=" " v-model="newPwd">
                                            <input type="password" class="account_pwd" placeholder="  "
                                                   title="  " v-model="newCheck">
                                        </div>
                                        <div class="btns">
                                            <button type="button" class="btn01" @click="changePasswordClose"></button>
                                            <button type="button" class="btn02" @click="changePasswordAction">
                                            </button>
                                        </div>
                                    </fieldset>
                                </form>
                            </div>
                        </div>
                    </div>
                </modal>
                <modal v-if="passwordSend">
                    <div slot="body">
                        <!--     -->
                        <div class="pw_send_view popup">
                            <div class="popup_wrap">
                                <div class="tit">
                                    <p>  </p>
                                    <span class="close" @click="passwordSendClose"><img
                                        src="../../../static/img/esc_icon.png" alt=""></span>
                                </div>
                                <p class="link_txt">{{accountData.email}} ()<br>
                                      .</p>
                                <div class="btns">
                                    <button type="button" class="btn02" @click="passwordSendClose"></button>
                                </div>
                            </div>
                        </div>
                    </div>
                </modal>
                <modal v-if="userExit">
                    <div slot="body">
                        <!--     -->
                        <div class="withdarw_view popup">
                            <div class="popup_wrap">
                                <div class="tit">
                                    <p></p>
                                    <span class="close" @click="userExitClose"><img
                                        src="../../../static/img/esc_icon.png" alt=""></span>
                                </div>
                                <p class="link_txt"> VORA   ?<br>
                                          .<br>
                                      ?</p>
                                <div class="btns">
                                    <button type="button" class="btn01"></button>
                                    <button type="button" class="btn02"></button>
                                </div>
                            </div>
                        </div>
                    </div>
                </modal>
                <modal v-if="emailCert">
                    <div slot="body">
                        <!--    -->
                        <div class="email_certification popup">
                            <div class="popup_wrap">
                                <div class="tit">
                                    <p> </p>
                                    <span class="close" @click="emailCertClose"><img
                                        src="../../../static/img/esc_icon.png" alt=""></span>
                                </div>
                                <p class="link_txt">
                                    <span class="email_pop_txt">{{accountData.email}} ()</span>
                                       <br>
                                    VORA     </p>
                                <div class="btns">
                                    <button type="button" class="btn02" @click="sendVerificationMail"> </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </modal>
                <div class="main_inner">
                    <div class="set_content">
                        <SideMenu></SideMenu>
                        <div class="right_con">
                            <h3></h3>
                            <div class="join_day">
                                <p>  {{changeDate(accountData.accountCreateAt)}}</p>
                            </div>
                            <h4> </h4>
                            <div class="email_add">
                                <p class="txt01">{{accountData.email}}</p>
                                <div class="confirm_01" v-if="accountData.emailCertYn == 'N'">
                                    <p> </p>
                                </div>
                                <button type="button" v-if="accountData.emailCertYn != 'Y'"
                                        @click="emailCert = !emailCert"> 
                                </button>
                                <p class="confirm_days" v-if="accountData.emailCertYn == 'Y'">
                                    {{changeDate(accountData.emailCertDate)}}</p>
                            </div>
                            <div class="pw_change">
                                <h4></h4>
                                <button type="button" class="on" @click="changePassword = !changePassword"> 
                                </button>
                                <button type="button" class="pw_send" @click="sendTempPwd"> 
                                    
                                </button>
                            </div>
                            <div class="sns_linkage">
                                <h4>SNS </h4>
                                <button type="button" class="facebook">    {{accountData.facebook ? '' :
                                    ''}}
                                </button>
                                <button type="button" class="google">    {{accountData.google ? '' : ''}}
                                </button>
                                <button type="button" class="naver">    {{accountData.naver ? '' : ''}}
                                </button>
                                <button type="button" class="kakao">    {{accountData.kakao ? '' : ''}}
                                </button>
                            </div>
                            <a href="#" class="withdraw_btn" @click="userExit = !userExit"></a>
                        </div>
                    </div>
                    <div class="intro_txt">
                        <p><a href="#">VORA</a>  <a href="#"></a>  <a href="#"></a>  <a
                            href="#"></a></p>
                        <p class="app_icon"><a href="#" class="iphone">iPhone </a><a href="#" class="android">Android
                            </a></p>
                        <p class="copyright">Copyright @ KYOBO BOOK CENTRE.</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Modal from '../../components/inc/Modal'
    import SideMenu from '../../components/inc/SideMenu'
    import {mapGetters, mapActions} from 'vuex'

    export default {
        name: 'SetProfile',
        components: {
            Modal,
            SideMenu
        },
        created: function () {
            this.findAccountData();
        },
        computed: {
            ...mapGetters([
                'getAccountIdx',
                'getUserIdx'
            ])
        },
        data: function () {
            return {
                changePassword: false,
                passwordSend: false,
                userExit: false,
                emailCert: false,
                accountData: {},
                oldPwd: "",
                newPwd: "",
                newCheck: ""
            }
        },
        methods: {
            ...mapActions([
                'getAccountDataAct',
                'callCertMail',
                'sendTempPassword',
                'checkPwdAndChange'
            ]),
            changePasswordClose: function () {
                this.changePassword = false;
                this.oldPwd = "";
                this.newPwd = "";
                this.newCheck = "";
            },
            passwordSendClose: function () {
                this.passwordSend = false;
            },
            userExitClose: function () {
                this.userExit = false;
            },
            emailCertClose: function () {
                this.emailCert = false;
            },
            findAccountData: function () {
                this.$store.dispatch('getAccountDataAct', {idx: this.getAccountIdx}).then((response) => {
                    this.accountData = response.returnData;
                }).catch((response) => {

                })
            },
            changeDate: function (str) {
                return this.$moment(str).format("YYYY MM DD");
            },
            sendVerificationMail: function () {
                this.$store.dispatch('callCertMail', {idx: this.getUserIdx}).then((response) => {
                    if (response.code == "OK") {
                        this.emailCertClose()
                    }
                }).catch((response) => {
                    console.log(response)
                })
            },
            sendTempPwd: function () {
                this.passwordSend = !this.passwordSend;
                this.$store.dispatch('sendTempPassword', {email: this.accountData.email}).then((response) => {
                    if (response.code == "OK") {
                        this.emailCertClose()
                    }
                }).catch((response) => {
                    console.log(response)
                })
            },
            changePasswordAction: function () {
                if (!this.validatePassword(this.oldPwd)) {
                    console.log("error1")
                    return;
                }
                if (!this.validatePassword(this.newPwd)) {
                    console.log("error2")
                    return;
                }
                if (this.newPwd != this.newCheck) {
                    console.log("error3")
                    return;
                }
                console.log("allpass");
                this.$store.dispatch('checkPwdAndChange', {
                    email: this.accountData.email,
                    userPwd: this.oldPwd,
                    newPassword: this.newPwd
                }).then((response) => {
                    console.log(response.code);
                    if (response.code == "OK") {
                        this.changePassword = !this.changePassword;
                    }
                }).catch((response) => {

                })
            },
            validatePassword: function (pass) {
                const re = /^(?=.*[0-9])(?=.*[a-z])(?=.*[$@!%*#?&])[a-z0-9$@!%*#?&]{8,}$/;
                return re.test(String(pass).toLowerCase());
            },
        }
    }
</script>

<style scoped>
    .account_pwd {
        color: #111111;
    }
</style>



