<template>
    <div id="A_Container_Wrap">
        <div id="A_Container">
            <Modal v-if="passwordSend">
                <div slot="body">
                    <!--     -->
                    <div class="pw_send_view popup">
                        <div class="pw_send_wrap popup_wrap">
                            <div class="tit">
                                <p>  </p>
                                <span class="close" @click="passwordSend = !passwordSend"><img src="../../../static/img/esc_icon.png" alt=""></span>
                            </div>
                            <p class="link_txt">{{email}} ()<br>
                                  .</p>
                            <div class="btns">
                                <button type="button" class="btn02" @click="goLogin"></button>
                            </div>
                        </div>
                    </div>
                </div>
            </Modal>
            <!--         -->
            <Modal v-if="notJoinUser">
                <div slot="body">
                    <div class="pw_send_view02">
                        <div class="pw_send_wrap02">
                            <div class="tit">
                                <p>  </p>
                                <span class="close" @click="notJoinUser = !notJoinUser"><img src="../../../static/img/esc_icon.png" alt=""></span>
                            </div>
                            <p class="link_txt">{{email}} ()<br>
                                  .<br>
                                  .</p>
                            <div class="btns">
                                <button type="button" class="btn02" @click="notJoinUser = !notJoinUser"></button>
                            </div>
                        </div>
                    </div>
                </div>
            </Modal>
            <div class="bg_grey">
                <div class="re_member_contents">
                    <h1 class="logo"><img src="../../../static/img/login_logo.png" alt="vora"></h1>
                    <h2 class="re_txt01">  </h2>
                    <h3 class="re_txt02">      </h3>
                    <form>
                        <fieldset>
                            <!--<legend>    </legend>-->
                            <div id="emailDiv_re" class="join_input">
                                <input type="text" placeholder=" " title=" " v-model="email">
                                <span class="email_check"></span>
                            </div>
                            <div class="login_btns">
                                <button type="button" class="btn01" @click="sendMailService">  </button>
                            </div>
                        </fieldset>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Modal from '../inc/Modal'
    import {mapActions} from 'vuex'

    export default {
        name: 'PasswordSend',
        components: {
            Modal
        },
        data: function () {
            return {
                passwordSend: false,
                notJoinUser : false,
                email : ""
            }
        },
        methods : {
            ...mapActions([
                'sendTempPassword',
                'checkUserMailAct'
            ]),
            validateEmail : function (email) {
                const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                return re.test(String(email).toLowerCase());
            },
            sendMailService : function () {

                if(this.validateEmail(this.email)){
                    this.$store.dispatch('checkUserMailAct', {email : this.email}).then((response)=>{
                        console.log("success",response);
                        if(response.code == "OK"){
                            this.passwordSend = !this.passwordSend;
                            if(this.validateEmail(this.email)){
                                this.$store.dispatch('sendTempPassword', {email : this.email}).then((response)=>{
                                    if(response.code == "OK"){
                                    }
                                }).catch((response)=>{
                                    console.log(response)
                                })
                            }
                        }else{
                            this.notJoinUser = !this.notJoinUser;
                        }
                    }).catch((response)=>{
                        console.log("error",response)
                    })
                }
            },
            goLogin : function () {
                this.passwordSend = !this.passwordSend;
                this.$router.push({name : 'Login'});
            }
        }
    }
</script>

<style scoped>

</style>



