<template>
    <Modal v-if="blockUser">
        <div slot=body>
            <!--   -->
            <div class="block_view">
                <div class="block_wrap">
                    <div class="tit">
                        <p></p>
                        <span class="close" @click="close"><img src="../../../static/img/esc_icon.png" alt=""></span>
                    </div>
                    <p class="block_txt">{{blockUser.nickName}} ?<br>
                         {{blockUser.nickName}}    .</p>
                    <div class="btns">
                        <button type="button" class="btn01" @click="close"></button>
                        <button type="button" class="btn02" @click="toggleBlockMemberAction"></button>
                    </div>
                </div>
            </div>
        </div>
    </Modal>
</template>

<script>
import {mapState, mapActions, mapGetters} from 'vuex'
import Modal from '../inc/Modal'

export default {
    name: 'BlockMemberModal',
    components : {
        Modal
    },
    data: function(){
        return {
            blockUser : false,
        }
    },
    methods : {
        show : function(member){
            this.blockUser = member;
        },
        toggleBlockMemberAction: function(){
            this.$store.dispatch('toggleMemberBlocmMemberAct',this.blockUser.idx);
            this.blockUser = false;
        },
        close : function(){
            this.blockUser = false;
        }
    }
}
</script>
<style scoped>

</style>



