<template>
    <div id="A_Container_Wrap">
        <div id="A_Container">
            <modal v-if="duplicationId">
                <div slot="body">
                    <div class="id_notice_view">
                        <div class="id_notice_wrap">
                            <div class="tit">
                                <p>    </p>
                                <span class="close"><img src="../../static/img/esc_icon.png" alt=""></span>
                            </div>
                            <p class="link_txt">     .<br>
                                       <br>
                                     <br>
                                    </p>
                            <div class="btns">
                                <button type="button" class="btn01"> IP </button>
                                <button type="button" class="btn02"> </button>
                            </div>
                        </div>
                    </div>
                </div>
            </modal>
            <!--   -->
            <modal v-if="nonSignUp">
                <div slot="body">
                    <div class="id_stop_view">
                        <div class="id_stop_wrap">
                            <div class="tit">
                                <p> </p>
                                <span class="close"><img src="../../static/img/esc_icon.png" alt=""></span>
                            </div>
                            <p class="link_txt">  .<br>
                                    <br>
                                    </p>
                            <div class="btns">
                                <button type="button" class="btn01"></button>
                                <button type="button" class="btn02">  </button>
                            </div>
                        </div>
                    </div>
                </div>
            </modal>
            <!--    -->
            <modal v-if="stopId">
                <div slot="body">
                    <div class="pw_send_view popup">
                        <div class="popup_wrap">
                            <div class="tit">
                                <p> </p>
                                <span class="close" @click="closeStopId"><img src="../../static/img/esc_icon.png"
                                                                              alt=""></span>
                            </div>
                            <p class="link_txt">   .<br>
                                    .</p>
                            <div class="btns">
                                <button type="button" class="btn02"></button>
                            </div>
                        </div>
                    </div>
                </div>
            </modal>
            <div class="bg_grey">
                <div class="login_contents">
                    <h1 class="logo">
                        <span class="logo_txt01"> </span>
                        <img src="../../static/img/login_logo.png" alt="vora">
                        <span class="logo_txt02">   .</span>
                    </h1>
                    <form>
                        <fieldset>
                            <!--<legend>vora login</legend>-->
                            <input type="text" placeholder=" " title=" " v-model="email">
                            <input type="password" placeholder="" title="" v-model="userPwd"
                                   @keyup.enter="goLogin">
                        </fieldset>
                    </form>
                    <div class="login_btns">
                        <button type="button" class="btn01" @click="goLogin"></button>
                    </div>
                    <div class="join_password">
                        <p>
                            <router-link :to="{path : '/Join' }" class="write_icon" tag="a"
                                         v-text="''"></router-link>
                            <router-link :to="{path : '/PasswordSend' }" class="write_icon" tag="a"
                                         v-text="' '"></router-link>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import http from '../http'
    import Modal from "./inc/Modal"
    import {mapState, mapActions, mapGetters} from 'vuex'
    import global from '../global'

    export default {
        name: "Login",
        components: {
            Modal
        },
        data: function () {
            return {
                duplicationId: false,
                nonSignUp: false,
                stopId: false,
                email: "",
                userPwd: "",
                deviceType: ""
            }
        },
        created: function () {
            this.checkType();
            console.log(this.deviceType);
        },
        methods: {
            ...mapActions([
                'setDeviceInfo'
            ]),
            goLogin: function () {
                if (this.email != "" && this.userPwd != "") {
                    let account = {
                        email: this.email,
                        userPwd: this.userPwd,
                        deviceType: this.deviceType
                    }

                    http.createAxios().post(global.baseURI + global.api.post.member.signin, account, global.config).then((response) => {
                        console.log(response.data);
                        if (response.data.code == "OK") {
                            //  
                            if (response.data.returnData.accessToken) {
                                this.$store.commit('login', response.data.returnData);
                            }
                            this.$router.push({'name': 'MainFeed'});
                            // this.$router.push({'name': 'StepTwo'});
                        } else if (response.data.code == "SYSTEM_DB_FIND_FAIL") {


                        } else if (response.data.code == "COMMON_LOGIN_FAIL_EXPIRE_CERT_NUM") {


                        }
                        else {


                        }

                    })
                        .catch(response => {
                            console.log(response);
                        });
                }


            },
            closeStopId: function () {
                this.stopId = false;
            },
            checkType : function () {
                let mobile = (/iphone|ipad|ipod|android/i.test(navigator.userAgent.toLowerCase()));
                if (mobile) {

                    //   OS .
                    let userAgent = navigator.userAgent.toLowerCase();

                    if (userAgent.search("android") > -1)

                        this.deviceType = "MOBILE(AOS)";

                    else if ((userAgent.search("iphone") > -1) || (userAgent.search("ipod") > -1)

                        || (userAgent.search("ipad") > -1))

                        this.deviceType = "MOBILE(IOS)";
                    else
                        this.deviceType = "MOBILE(ETC)";

                } else {
                    //   
                    this.deviceType = "WEB";
                }
                console.log("dispatch Login",this.deviceType);
                this.$store.dispatch('setDeviceInfo', this.deviceType);

            }
        }

    }
</script>

<style scoped>

</style>



