<template>
    <Modal v-if="deleteObjectInfo">
        <div slot=body>
            <!--   -->
            <div class="delete_view">
                <div class="delete_wrap">
                    <div class="tit">
                        <p></p>
                        <span class="close" @click="close()"><img
                            src="../../../static/img/esc_icon.png" alt=""></span>
                    </div>
                    <p class="block_txt" v-if="objectType=='feed'">  ?</p>
                    <p class="block_txt" v-if="objectType=='comment'">  ?</p>
                    <p class="block_txt" v-if="objectType=='commentMaster'">  ?     .</p>
                    <div class="btns">
                        <button type="button" class="btn01" @click="close()"></button>
                        <button type="button" class="btn02" @click="doDeleteFeed()"></button>
                    </div>
                </div>
            </div>
        </div>
    </Modal>
</template>

<script>
import Modal from '../inc/Modal'
export default {
    name:"DeleteModal",
    props:['afterFunction'],
    components : {
        Modal
    },
    data : function(){
        return {
            deleteObjectInfo : false,
            objectType : "",
        }
    },
    methods : {
        close: function(){
            this.deleteObjectInfo = false;
            this.objectType = "";
        },
        show : function(object,objectType){
            this.deleteObjectInfo = object;
            this.objectType = objectType;
        },
        doDeleteFeed : function (){
            let action;
            if(this.objectType=="feed")
                action = "deleteFeedAct";
            else
                action = "deleteCommentAct"
            this.$store.dispatch(action,this.deleteObjectInfo).then((response)=>{
                this.afterFunction(this.objectType);
                this.close();
            }).catch((response)=>{
            });
        }
    }
}
</script>
<style scoped>

</style>



