<template>
    <div id="A_Container_Wrap">
        <div id="A_Container">
            <div class="bg_rgba">
            </div>
            <Modal v-if="inquirys">
                <div slot="body">
                <!--     -->
                <div class="business_change_view popup">
                    <div class="business_change_view_wrap popup_wrap">
                        <div class="tit">
                            <p>  </p>
                            <span class="close" @click="inquirysClose"><img src="../../../static/img/esc_icon.png" alt=""></span>
                        </div>
                        <p class="link_txt">    .<br>
                                <br>
                             (sample@mail.com)()<br>
                             . .</p>
                        <div class="btns">
                            <button type="button" class="btn02" @click="inquirysClose"></button>
                        </div>
                    </div>
                </div>
                </div>
            </Modal>
            <div class="bg_grey">
                <div class="main_inner">
                    <div id="A_Container">
                        <div class="set_content">
                            <SideMenu></SideMenu>
                            <div class="right_con">
                                <h3>         .</h3>
                                <div class="set_business">
                                    <form>
                                        <fieldset>
                                            <legend>  </legend>
                                            <h4>   </h4>
                                            <p>
                                                <select class="select01" v-model="type">
                                                    <option value=""></option>
                                                    <option value=""></option>
                                                    <option value=""></option>
                                                </select>
                                            </p>
                                            <h4></h4>
                                            <p>
                                                <textarea placeholder="    ." v-model="content"></textarea>
                                            </p>
                                            <h4>   </h4>
                                            <p>
                                                <input type="text" placeholder="   " v-model="email">
                                            </p>
                                            <input type="checkbox" id="business_check" v-model="agree_check"><label for="business_check"><span></span> .</label>
                                            <div class="save_btns">
                                                <button type="button" @click="saveInquiry"></button>
                                            </div>
                                        </fieldset>
                                    </form>
                                </div>
                            </div>
                        </div>
                        <div class="intro_txt">
                            <p><a href="#">VORA</a>  <a href="#"></a>  <a href="#"></a>  <a href="#"></a></p>
                            <p class="app_icon"><a href="#" class="iphone">iPhone </a><a href="#" class="android">Android </a></p>
                            <p class="copyright">Copyright @ KYOBO BOOK CENTRE.</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  import {mapState, mapActions, mapGetters} from 'vuex'
  import global from '../../global'

  import Modal from '../../components/inc/Modal'
  import SideMenu from '../../components/inc/SideMenu'
  export default {
    name: 'SetInquiry',
    components : {
      Modal, SideMenu
    },
    data : function () {
      return {
        inquirys : false,
        type : "",
        content : "",
        email : "",
        agree_check : false,
      }
    },
    methods : {
      ...mapActions(['saveInquiryAct']),
      inquirysClose : function () {
        this.inquirys = false
      },
      saveInquiry : function(){
        let payload = {
          "type":this.type,
          "content":this.content,
          "email":this.email,
          "agree_check":this.agree_check
        };
        if(this.agree_check){
          this.saveInquiryAct(payload).then((response)=>{
            if(response){
              this.inquirys = !this.inquirys
            }
          }).catch((error)=>{
          });
        }else{
          alert("");
        }
      }
    }
  }
</script>

<style scoped>

</style>



