                            <ul class="lnb">
                                <li @click="triggerMenu()">
                                    <router-link to="/SetProfile" tag="a"> </router-link>
                                </li>
                                <li @click="triggerMenu()">
                                    <router-link to="/SetAccount" tag="a" class="on"></router-link>
