                                </li>
                                <li @click="triggerMenu()">
                                    <router-link to="/SetNotice" tag="a"></router-link>
                                </li>
                                <li @click="triggerMenu()">
                                    <router-link to="/SetHelp" tag="a"></router-link>
                                </li>
                                <li @click="triggerMenu()">
                                    <router-link to="/SetInquiry" tag="a">  </router-link>
                                </li>
                                <li @click="triggerMenu()">
