                                </li>
                            </ul>
                            <div class="bot_txt">
                                <div class="intro_txt">
                                    <p><a href="https://www.vora.co.kr/feed/post.asp?idx=1679">VORA</a>  <a
                                        href="/inquiry/">  </a>  <a href="../data/data01.asp"></a>  <a
                                        href="../data/data02.asp"></a></p>
